@import "variables";


body {
	h1, h2, .h1, .h2 {
		font-weight:400;
	}

	h1, .h1 {
		font-size: 36px;
		line-height:30px;
	}

	h2, .h2 {
		margin-top:32px;
		line-height:30px;
		font-size:24px;
	}

	h3, .h3 {
		font-size:18px;
		line-height:30px;
	}

	p {
		text-align:justify;
		line-height:30px;
		margin-bottom:24px;
	}

	p:last-child {
		margin-bottom:0;
	}

	header h1, h1:first-child {
		margin-top:0;
	}
}

.meta {
	margin-bottom:24px;
}

.navbar-brand {
	padding-top:2.5px;
	padding-bottom:2.5px;
	opacity:0.9;
}

.navbar-brand:hover {
	opacity:1;
}

#navbar-main {
	background:#FFFFFC;
	-moz-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.08);
	-webkit-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.08);
	box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.08);
}

.navbar-toggle .icon-bar {
	background-color:#ccc;
}

.container h2:first-child {
	margin-top:0;
}

article {
	border-bottom:1px solid #ccc;
	padding-bottom:20px;
	margin-bottom:20px;
}

article:last-child {
	border-bottom:0;
	margin-bottom:0;
}

.alert-top {
	margin-top:0;
	margin-bottom:0;
	border-radius:0;
}

.margin-top {
	margin-top:15px;
}

.alert.margin-top {
	margin-top:25px;
}

.margin-bottom {
	margin-bottom:15px;
}

body .alert-cookie {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	background: #fff;
	border-bottom: 1px solid #eee;
}

body .alert-cookie .btn {
	margin-bottom:15px;
}

body .alert {
	margin-bottom: 25px;
}

body .alert-warning {
	color:#CC8416;
}

.media .media-left .glyphicon {
	font-size:18px;
	margin-left:5px;
}

.alert-warning .media .media-left .glyphicon {
	margin-top:6px;
}

@media (max-width: $screen-xs-max) {
	body .alert-cookie {
		position: static;
	}
	body .alert-cookie .btn {
		width:100%;
	}
}

body .footer {
	border-top:1px solid #ccc;
}

body p q {
	font-style: italic;
}

.tags {
	margin-top:15px;
}

.btn-readmore {
	margin-top:10px;
}

@media (max-width: $screen-sm-max) {
	.text-sm-center {
		text-align:center;
	}
	.text-sm-left {
		text-align:left;
	}
	.text-sm-right {
		text-align:right;
	}
	.text-sm-justify {
		text-align:justify;
	}

	@media (max-width: $screen-xs-max) {
		.text-xs-center {
			text-align:center;
		}
		.text-xs-left {
			text-align:left;
		}
		.text-xs-right {
			text-align:right;
		}
		.text-xs-justify {
			text-align:justify;
		}
	}
}

.meta {
	font-size:80%;
	color:#8c8c8c;
}

body {
	/*background: $brand-light url('../img/background.png');*/
	background: $brand-light url('data:image/png;base64,');
}
